* {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font-family: "Saira Condensed";
}
.signup-page {
  background: url(../../../assets/images/BACKGROUND.png) no-repeat;
  background-size: cover;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  .signup-content {
    width: 60%;
    height: 94%;
    display: flex;
    .left-content {
      background: url(../../../assets/images/login1.png);
      background-size: cover;
      width: 50%;
      height: 100%;
      .logo-name {
        display: flex;
        width: 264px;
        height: 64px;
        margin-top: 30px;
        margin-left: 20px;
        align-items: center;
        gap: 11px;
        .logo {
          background: url(../../../assets/images/logo_png.png);
          background-size: cover;
          width: 20%;
          height: 100%;
        }
        .name {
          color: #fff;
          font-family: "Saira Condensed";
          font-size: 32px;
          font-style: normal;
          font-weight: 700;
          line-height: normal;
        }
      }
    }
    .right-content {
      background: #e7cdbf;
      box-shadow: 4px 4px 8px 3px rgba(0, 0, 0, 0.25);
      width: 50%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 15px;
      .signup-form {
        display: flex;
        width: 80%;
        height: 90%;
        flex-direction: column;
        align-items: flex-start;
        gap: 30px;
        flex-shrink: 0;
        .error-message {
          color: red;
          font-size: 24px;
          font-weight: 600;
        }
        .signup-form-title {
          font-family: "Saira Condensed";
          font-size: 40px;
          font-style: normal;
          font-weight: 600;
          line-height: normal;
        }
        .signup-form-content {
          display: flex;
          height: 424px;
          flex-direction: column;
          justify-content: space-between;
          align-self: stretch;
          gap: 10px;
          .full-name {
            display: flex;
            width: 100%;
            justify-content: space-between;
            .first-name,
            .last-name {
              width: 45%;
              height: 58px;
              flex-shrink: 0;
              .first-name-title,
              .last-name-title {
                font-size: 16px;
                font-style: normal;
                font-weight: 500;
                line-height: normal;
              }
              .first-name-input,
              .last-name-input {
                width: 100%;
                border: 1px solid black;
                text-indent: 5px;
                outline: none;
                height: 34px;
              }
            }
          }
          .email {
            .email-title {
              font-size: 16px;
              font-style: normal;
              font-weight: 500;
              line-height: normal;
            }
            .email-input {
              width: 100%;
              border: 1px solid black;
              text-indent: 5px;
              outline: none;
              height: 34px;
            }
          }
          .password {
            .password-title {
              font-size: 16px;
              font-style: normal;
              font-weight: 500;
              line-height: normal;
            }
            .password-input {
              width: 100%;
              border: 1px solid black;
              text-indent: 5px;
              outline: none;
              height: 34px;
            }
          }
          .confirm-password {
            .confirm-password-title {
              font-size: 16px;
              font-style: normal;
              font-weight: 500;
              line-height: normal;
            }

            .confirm-password-input {
              width: 100%;
              border: 1px solid black;
              text-indent: 5px;
              outline: none;
              height: 34px;
            }
          }
          .phone-number {
            .phone-number-title {
              font-size: 16px;
              font-style: normal;
              font-weight: 500;
              line-height: normal;
            }

            .phone-number-input {
              width: 100%;
              border: 1px solid black;
              text-indent: 5px;
              outline: none;
              height: 34px;
            }
          }
          .signup-and-login {
            display: flex;
            height: 80px;
            flex-direction: column;
            justify-content: flex-end;
            align-items: center;
            gap: 10px;
            flex-shrink: 0;
            align-self: stretch;
            margin-top: 5px;
            .signup-btn {
              display: flex;
              height: 34px;
              width: 100%;
              justify-content: center;
              align-items: center;
              //cần đồng bộ màu với login
              background: linear-gradient(
                90deg,
                #867375 0%,
                #493a3b 46%,
                #0a0909 98%
              );
              color: #f3f3f3;
              font-size: 15px;
              font-weight: 700;
              letter-spacing: 3.6px;
            }
            .btn-active {
              background: linear-gradient(
                90deg,
                #df0012 0%,
                #8b000d 46%,
                #e91324 98%
              );
            }
            .text-with-login {
              font-size: 10px;
              font-style: normal;
              font-weight: 275;
              line-height: normal;
              .login {
                color: #df0012;
                font-size: 12px;
                font-weight: 300;
                line-height: normal;
                text-decoration: none;
              }
            }
          }
        }
        .note {
          font-size: 12px;
          font-style: normal;
          font-weight: 275;
          line-height: normal;
          color: #121212;
          .term-of-service,
          .private-policy {
            font-size: 12px;
            font-style: normal;
            font-weight: 275;
            line-height: normal;
            color: #121212;
          }
        }
      }
    }
  }
}
